import { graphql, Link } from "gatsby"
import React, { useRef } from "react"
import Layout from "../components/layout"
import styled from "styled-components"
import Content from "../components/content"
import { useScrollTop } from "../hooks/useScrollTop"
import { WindowLocation } from "@reach/router"
import { MenuType } from "../components/types"

type LocationStateType = {
  prevScrollTop: number
}

type PageType = {
  html: string
  fields: {
    slug: string
    parentSlug: string
  }
}

type PageDataType = {
  parent: PageType
  page: PageType
  menu: MenuType
}

type PagePropsType = {
  data: PageDataType
  location: WindowLocation & { state: LocationStateType }
}

export default ({ data, location }: PagePropsType) => {
  const pageRef = useRef<HTMLDivElement>(null)
  const scrollTop = useScrollTop(pageRef, location?.state?.prevScrollTop)

  return (
    <Layout>
      <Page ref={pageRef}>
        {data.parent && <Content html={data.parent.html} />}
        {data.menu.edges.length > 0 && (
          <ul>
            {data.menu.edges.map(({ node }) => (
              <li key={node.id}>
                <Link
                  to={`/${node.parentSlug}${node.path}`}
                  state={{ prevScrollTop: scrollTop }}
                >
                  {node.title}
                </Link>
              </li>
            ))}
          </ul>
        )}
        <Content html={data.page.html} />
      </Page>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!, $parentSlug: String) {
    page: markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      fields {
        slug
        parentSlug
      }
    }
    parent: markdownRemark(fields: { slug: { eq: $parentSlug, ne: null } }) {
      html
      fields {
        slug
        parentSlug
      }
    }
    menu: allSectionMenuJson(filter: { parentSlug: { eq: $parentSlug } }) {
      edges {
        node {
          id
          path
          title
          parentSlug
        }
      }
    }
  }
`

const Page = styled.div`
  overflow-y: auto;
  padding: 2rem;
  flex-basis: 100%;
  iframe {
    width: 100%;
    border: none;
    overflow: visible;
    flex-basis: 100%;
  }
`
